@use '../../sass/abstracts/variables';

.Footer {
  display: flex;
  justify-content: center;
}

.Link {
  font-size: 10rem;
  color: #2760ff;
  margin: 1rem 5rem;
  transition: all 0.2s cubic-bezier(0.1, 0.3, 0.3, 1);
  &:hover {
    transform: rotate(-10deg) scale(1.2);
  }

  @media screen and (max-width: variables.$small-width) {
    margin: 1rem 2rem;
  }
}
